.menu {
	&:not(:last-child) {
		@include adaptiv-value("margin-right", 20, 15, 1);
	}

	// .menu__burger

	&__burger {}

	// .menu__body

	&__body {
		@include adaptiv-value("max-width", 425, 320, 1);
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: -100%;
		z-index: 200;
		transition: left .5s ease .0s;
		filter: drop-shadow(0px 6px 14px rgba(0, 0, 0, 0.2));
		border-right: 1px solid $colorBorderGrey;
		background-color: $colorWhite;

		&._active {
			left: 0;
		}

		@media (max-width: 374.98px) {
			max-width: none;
		}
	}

	// .menu__wrapper

	&__wrapper {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		opacity: 0;
		pointer-events: none;
		z-index: 190;
		transition: opacity .5s ease .0s;
		background-color: $colorWrapper;

		// .header__actions._active

		&._active {
			opacity: 0.5;
			pointer-events: auto;
		}
	}

	// .menu__top

	&__top {
		padding: 5px 15px;
		width: 100%;
		@include adaptiv-value("min-height", 80, 70, 1);
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid $colorBorderGrey;
	}

	// .menu__content

	&__content {
		padding: 20px 15px;
	}
}

.menu-top {

	// .menu-top__logo

	&__logo {
		&:not(:last-child) {
			margin-right: 15px;
		}
	}

	// .menu-top__close

	&__close {
		width: 40px;
		height: 40px;
		border-radius: 6px;

		&:before,
		&:after {
			background-color: $colorDark;
		}

		@media (any-hover: hover) {
			transition: background-color .2s ease .0s;

			&:hover {
				background-color: $colorOrangeHover;
			}
		}
	}
}

.menu-content {

	// .menu-content__work

	&__work {
		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}

	// .menu-content__category

	&__category {
		width: 100%;
		height: 55px;
		justify-content: flex-start;

		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}

	// .menu-content__item

	&__item {
		padding: 10px 5px;
		width: 100%;
		min-height: 55px;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		border-radius: 8px;

		text-decoration: none;

		&:not(:last-child) {
			margin-bottom: 10px;
		}

		@media (any-hover: hover) {
			transition: background-color .2s ease .0s;

			&:hover {
				background-color: $colorOrangeHover;
			}
		}
	}

	// .menu-content__item-icon

	&__item-icon {
		width: 35px;
		height: 35px;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		border-radius: 6px;
		background-color: $colorOrangeHover;

		stroke-width: 2px;
		stroke: $colorDark;

		&:not(:last-child) {
			margin-right: 20px;
		}
	}

	// .menu-content__item-text

	&__item-text {
		flex: 1 1 auto;

		font-size: 16px;
		font-weight: 500;
		line-height: 20 / 16 * 100%;
		color: $colorDark;
	}

	// .menu-content__item-value

	&__item-value {
		margin-right: 8px;
		width: 20px;
		height: 20px;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		background-color: $colorOrange;

		font-size: 14px;
		color: $colorWhite;
	}
}