.main-slider {

	&:not(:last-child) {
		@include adaptiv-value("margin-bottom", 80, 30, 1);
	}

	// .main-slider__body

	&__body {
		position: relative;
		overflow: hidden;
		border-radius: 20px;

		.swiper-slide {
			@include adaptiv-value("padding-top", 65, 40, 1);
			@include adaptiv-value("padding-right", 75, 15, 1);
			@include adaptiv-value("padding-left", 75, 15, 1);
			padding-bottom: 65px;
			@include adaptiv-value("min-height", 550, 300, 1);
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			position: relative;
			overflow: hidden;
			border-radius: 20px;
			border: 1px solid $colorBorderGrey;

			@media (max-width: 479.98px) {
				align-items: center;
			}
		}

		.swiper-pagination {
			display: flex;
			align-items: center;
			position: absolute;
			bottom: 20px;
			left: 50%;
			transform: translateX(-50%);
		}
	}

	// .main-slider__loading

	&__loading {
		@include adaptiv-value("min-height", 550, 300, 1);
		display: flex;
		justify-content: center;
		align-items: center;
		overflow: hidden;
		border-radius: 20px;
		background-color: #f3f3f3;
	}

	// .main-slider__loading-icon

	&__loading-icon {
		display: inline-block;
		position: relative;
		width: 60px;
		height: 60px;

		div {
			box-sizing: border-box;
			display: block;
			position: absolute;
			width: 44px;
			height: 44px;
			margin: 6px;
			border: 6px solid #d3d3d3;
			border-radius: 50%;
			animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
			border-color: #d3d3d3 transparent transparent transparent;

			&:nth-child(1) {
				animation-delay: -0.45s;
			}

			&:nth-child(2) {
				animation-delay: -0.3s;
			}

			&:nth-child(3) {
				animation-delay: -0.15s;
			}

			@keyframes lds-ring {
				0% {
					transform: rotate(0deg);
				}

				100% {
					transform: rotate(360deg);
				}
			}
		}
	}

	// .main-slider__title

	&__title {
		@include adaptiv-value("max-width", 600, 270, 1);

		@include adaptiv-value("font-size", 52, 23, 1);
		font-weight: 600;
		line-height: 68 / 52 * 100%;
		color: $colorWhite;

		&:not(:last-child) {
			@include adaptiv-value("margin-bottom", 50, 15, 1);
		}

		@media (max-width: 479.98px) {
			flex: 1 1 auto;

			text-align: center;
		}
	}

	// .main-slider__button

	&__button {
		@include adaptiv-value("padding-right", 60, 40, 1);
		@include adaptiv-value("padding-left", 60, 40, 1);
	}

	// .main-slider__image

	&__image {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;

		@extend %unselectable;
	}
}